// zoom in
@mixin zoomIn($time) {
  animation: zoomIn $time;
  @keyframes zoomIn {
    from {
      transform: scale3d(0,0,0);
    }
  }
}

// zoom out
@mixin zoomOut($time) {
  animation: zoomOut $time;
  @keyframes zoomOut {
    to {
      transform: scale3d(0,0,0);
    }
  }
}

.zoomin, .zoom-in   { @include zoomIn(1s)  }
.zoomout, .zoom-out { @include zoomOut(1s) }
